import React, {Component} from 'react';
import {connect} from 'react-redux';

import Header from '../components/header';

import {changeAppVar} from '../actions/appActions'

class HeaderContainer extends Component{
  render(){
    const changeVar = {changeVar:(varName,value)=>this.props.changeVar(varName,value)}
    const app = Object.assign({},this.props.app,changeVar);
    return(
      <Header app={app}></Header>
    );
  }
}

const mapStateToProps = (state) =>{
  return{
    app: state.app
  };
}
const mapDispatchToProps = (dispatch) =>{
  return{
    changeVar:(varName,value)=>{
      dispatch(changeAppVar(varName,value));
    }
  };
}

export default connect(mapStateToProps,mapDispatchToProps)(HeaderContainer);



