import React, {Component} from 'react';

class Header extends Component{
  constructor(props){
    super(props);

    this.state = {
      name: this.props.app.name,
      editingName: false
    }

    this.nameInputKeypress = this.nameInputKeypress.bind(this);
    this.nameInputChanged = this.nameInputChanged.bind(this);
    this.nameInputBlur = this.nameInputBlur.bind(this);
    this.changeName = this.changeName.bind(this);
  }

  nameInputKeypress(e){
    const code = (e.keyCode ? e.keyCode : e.which);
    if(code === 13){
      this.changeName();
    }
  }

  nameInputChanged(e){
    this.setState({name:e.target.value});
  }
  nameInputBlur(){
    window.setTimeout(()=>{
      this.setState({editingName:false})
    },200);
  }
  changeName(){
    const name = this.editNameInput.value.trim();
    if(name !== ''){
      this.props.app.changeVar('name',name);
    }
    this.setState({editingName:false});
  }


  render(){
    const {app} = this.props;

    const name = () =>{
      if(this.state.editingName){
        return(
          <div>
            <input type="text" onKeyPress={this.nameInputKeypress} ref={(input) => {this.editNameInput = input}} onChange={this.nameInputChanged} value={this.state.name} onBlur={this.nameInputBlur}></input>
            <i className="fas fa-check" onClick={this.changeName} onFocus={()=>this.setState({editingName:true})}></i>
          </div>
        );
      }
      return <h2 onClick={()=>this.setState({editingName:true})}>{app.name}</h2>;
    }

    return (
      <div className="col-12 dark">
        {name()}
      </div>
    )
  }
}

export default Header;



