import React, { Component } from 'react';
import {connect} from 'react-redux';

import {DragDropContext} from 'react-beautiful-dnd';

import EffectContainer from './containers/EffectContainer';
import ControlsContainer from './containers/ControlsContainer';
import {moveSound, removeSoundReference, addSoundReference} from './actions/effectsActions';
import {changeSoundVar} from './actions/soundsActions';
import {changeAppVar} from './actions/appActions';

class App extends Component {
  constructor(props){
    super(props);
    this.onDragEnd = this.onDragEnd.bind(this);
    this.mediaButtonRef = this.mediaButtonRef.bind(this);
    this.state = {
      mediaButtons: {}
    }
  }

  //Beautiful DnD function
  onDragEnd(result){
    const {destination, source, draggableId} = result;
    if(destination === null){return;}
    if(destination.droppableId === source.droppableId){
      this.props.moveSound(destination.droppableId, draggableId, source.index, destination.index);
      return;
    }
    this.props.displaceSound(source.droppableId, destination.droppableId, draggableId, destination.index);
  }

  //Function for passing reference to effect media buttons up from children
  //In order to play sounds from sibling container
  mediaButtonRef(){
    //Function returns an object with 2 functions
    return {
      //Sets the reference to the play and pause buttons in the state
      setRef:(play, pause, id)=>{
        const ref = {[id]:{
          "play":play,
          "pause":pause
        }}
        this.setState(prevState =>({
          mediaButtons: Object.assign({}, prevState.mediaButtons,ref)
        }));
      },
      //Deletes the reference from the state on unload
      removeRef:(id)=>{
        this.setState(prevState =>({
          mediaButtons: delete prevState[id]
        }));
      }
    };
  }

  render() {
    return (
      <div className="App container-fluid">
        <div className="row justify-content-between">
          <div className="col-12 col-md-6 col-lg-4 col-xl-2">
            <ControlsContainer mediaButtons={this.state.mediaButtons} changeAppVar={this.props.changeAppVar}/>
          </div>
          <div className="col-12 col-md-6 col-lg-8 col-xl-10">
            <DragDropContext onDragEnd={this.onDragEnd}>
              <EffectContainer mediaButtonRef={this.mediaButtonRef}/>
            </DragDropContext>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) =>{
  return{
    effects: state.effects,
    sounds: state.sounds,
    app: state.app
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    moveSound: (effectId, soundId, oldIndex, newIndex) =>{
      dispatch(moveSound(effectId, soundId, oldIndex, newIndex));
    },
    displaceSound: (oldEffectId, newEffectId, soundId, index) =>{
      dispatch((dispatch)=>{
        dispatch(removeSoundReference(oldEffectId, soundId));
        dispatch(addSoundReference(newEffectId, soundId, index));
        dispatch(changeSoundVar(soundId,'effectId', newEffectId));
      });
    },
    changeAppVar:(varName,value)=>{
      dispatch(changeAppVar(varName,value));
    }
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);



